export function FileIcon({ lang }: { lang?: string }) {
  if (!lang) return <File />

  switch (lang.toLowerCase()) {
    case 'jsx':
    case 'tsx':
      return <React />
    case 'ts':
    case 'typescript':
      return <Ts />
    case 'javascript':
    case 'js':
    case 'mjs':
      return <Js />
    case 'json':
      return <Json />
    default:
      return <File />
  }
}

